@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);
* {
  box-sizing: border-box;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Avenir Next', sans-serif;
}


h1,
h2,
h3,
h4,
p {
  margin: 0;
}
.filter-view {
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: center;
}
.filter-view button {
  width: 110px;
  height: 40px;
  margin: 8px;
  background-color: #FEDF60;
  background-repeat: no-repeat;
  border: none;
  border-radius: 5px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16);
  font-size: 18px;
  font-family: helvetica;
}
@media (min-width: 992px) {
  .filter-view {
    width: 50%;
    margin: 0 50% 0 0;
    padding: 0;
  }
  .filter-view .type-filter {
    margin-left: 40px;
  }
  .filter-view .poke-filter {
    margin-right: 40px;
  }
}

.type-item {
  width: 70px;
  height: 40px;
  margin: 8px;
  background-color: #FFF;
  background-repeat: no-repeat;
  border: none;
  border-radius: 5px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16);
  font-size: 14px;
  font-family: helvetica;
}
@media (min-width: 992px) {
  .type-item {
    width: 80px;
    font-size: 16px;
    margin: 10px;
  }
  .scroll-text {
    display: none;
  }
}

.poke-image {
  height: 32px;
  width: 32px;
  background-size: 32px;
  margin: 5px;
  border: none;
  padding: 0;
  background-color: transparent;
}
.poke-container {
  padding: 0 10px 0px 10px;
  text-align: center;
  min-width: 55px;
}

.type-view {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 10px;
  background-color: #FEDF60;
  border-radius: 10px;
  overflow: auto;
  width: 100%;
}
@media (min-width: 992px) {
  .type-view {
    width: 48%;
    height: 485px;
    margin: 0px;
    justify-content: space-around;
  }
}

.variation-view {
  background-color: #FDBB32;
  border-radius: 10px;
  padding: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.info {
  width: 40%;
  padding: 5px 5px 5px 10px;
  font-family: helvetica;
}
.bronze {
  color: #CD7F32;
}
.silver {
  color: grey;
}
.gold {
  color: gold;
}
.exped {
  text-align: right;
  width: 60%;
}
.variation-pokemon-image {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
}
.variation-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 992px) {
  .exped-text {
    display: inline-block;
  }
  .info {
    width: 50%;
  }
}

.dish-view {
  background-color: #FEDF60;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  font-family: helvetica;
  overflow: auto;
  padding: 10px;
  margin: 10px;
  width: 100%;
}
.dish-name {
  text-align: center;
  font-size: 22px;
  padding-bottom: 20px;
}
.pokemon-view {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  background-color: #FDBB32;
  padding: 0px;
  margin-bottom: 5px;
  border-radius: 5px;
}
h3 {
  text-align: center;
  padding-bottom: 10px;
}
p {
  text-align: center;
  width: 100%;
  padding: 5px 0 5px 0;
}
@media (min-width: 992px) {
  .dish-view {
    width: 48%;
    height: 485px;
    margin: 0px;
  }
}

.App {
  background-color: #FDBB32;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.title {
  width: 100%;
  text-align: center;
  margin: 10px 0 10px 0px;
}
.title h1 {
  font-size: 24px;
}
.app-info {
  text-align: center;
  margin: 10px;
}
.cool-item a {
  padding-right: 10px;
}
.cool-item .poke-vault {
  width: 150px;
}
.cool-item .desk-cool {
  display: none;
}
.cool-item .mob-cool {
  display: block;
}
.cool-item iframe {
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 5px;
}
.strawpoll {
  width: 80vw;
  height: 120vw;
  border: 0;
  border-radius: 10px;
}
.hide {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
}
.hide a {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
}
.hide img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
}
.nimses {
  text-align: center;
  padding: 0 10px 0 10px;
}
@media (min-width: 992px) {
  .App {
    width: 800px;
    padding: 10px;
    margin: 50px auto;
    box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
    justify-content: space-around;
    border-radius: 10px;
  }
  .title {
    margin: 0px 0 10px 0px;
  }
  .strawpoll {
    width: 700px;
    height: 275px;
  }
  .desk-cool {
    display: block !important;
  }
  .mob-cool {
    display: none !important;
  }
  button {
    cursor: pointer;
  }
}

